import * as PIXI from "pixi.js";
import { Bat } from "./bat"

/**
 *
 */
export class BatInfected extends Bat {


  /**
   *
   * @param resources
   * @param skin
   */
  constructor(
    resources: PIXI.IResourceDictionary,
    skin: string
  ) {
    super(resources, skin);

    this._baseScale = 2.5

    // todo make this a red'isch filter
    const f = new PIXI.filters.ColorMatrixFilter()
    f.lsd(false)
    this.filters = [f]

    this.scale.x = this._baseScale
    this.scale.y = this._baseScale
  }
}