import * as PIXI from "pixi.js";
import { Config } from "./config";
import {
  Blood,
  Crosshair
} from "./objects"
import {
  ActorBase,
  Bat,
  BatInfected,
  EnemyBase,
  Player
} from "./actors"
import {
  HouseOutside,
  Loading,
  MaensionOutside,
  MapBase,
  Street
} from "./maps"
import {
  ActorStates,
  AnimationHelper,
  Gamepads,
  Keyboard,
  MathHelper
} from "./tools"
import {
  HUD
} from "./ui"
import { IDictionary } from "./tools/IDictionary"

let maps: IDictionary<MapBase> = {}

/**
 * @class
 */
export default class Game {
  private _stage: PIXI.Container

  private _hud?: HUD
  private _map?: MapBase
  private _player?: Player
  private _enemies: EnemyBase[] = [];
  private _mouseLocation?: PIXI.Point

  private _isRunning: boolean = true;

  private _startOffset?: number
  private _elapsedSeconds = 0;
  private readonly _resources: PIXI.IResourceDictionary

  private readonly _width: number
  private readonly _height: number

  private _mouseLine?: PIXI.Graphics
  private _crosshair?: Crosshair

  private static timeout: number = 300;

  /**
   *
   * @param resources
   * @param stage
   * @param width
   * @param height
   */
  constructor(
    resources: PIXI.IResourceDictionary,
    stage: PIXI.Container,
    width: number, height: number
  ) {
    this._stage = stage;

    this._resources = resources;
    this._width = width;
    this._height = height;

    maps.street = new Street(
      this._resources,
      this._width,
    );

    maps.house = new HouseOutside(
      this._resources,
      this._width,
    );

    maps.maension = new MaensionOutside(
      this._resources,
      this._width
    );

    maps.loading = new Loading(
      this._resources,
      this._width
    );

    this._init();
  }

  /**
   *
   * @private
   */
  private _init() {
    this._mouseLine = new PIXI.Graphics();
    this._crosshair = new Crosshair(this._resources)

    this._map = maps.maension!
    this._stage.addChild(this._map);
    this._stage.cursor = "none"

    // create player
    this._player = new Player(this._resources, "ryu");

    this._player.x = 60
    this._player.y = 400

    this._player!.adaptScale(this._player!.y, this._height)
    this._crosshair!.adaptScale(this._player!.y, this._height)

    // add player to stage
    this._map.actorContainer.addChild(this._player);

    const bat1 = new Bat(this._resources, "bat")
    bat1.flip()

    const bat2 = new Bat(this._resources, "bat")
    const bat3 = new BatInfected(this._resources, "bat")

    bat1.position.x = 900
    bat1.position.y = 200

    bat2.position.x = 920
    bat2.position.y = 220

    bat3.position.x = 880
    bat3.position.y = 240

    this._map.actorContainer.addChild(bat1);
    this._map.actorContainer.addChild(bat2);
    this._map.actorContainer.addChild(bat3);

    // attach the keyboard to player
    const gamepad = this._attachGamepad();

    // no gamepad, go to stinky keyboard
    if (!gamepad) {

      console.log("no gamepad, attaching stinky keyboard");
      this._attachKeyboard()
    }

    // init enemies
    this._enemies = [
      bat3
    ];

    this._hud = new HUD(
      this._resources,
      this._player,
      this._width,
      this._height
    );

    this._stage.addChild(this._hud);

    this._updateHud();

    this._attachMouse()

    this._updateMouseLocation(new PIXI.Point(this._width / 2, this._height / 2))

    this._startOffset = undefined;

    // this game is running baby
    this._isRunning = true;

    console.log(
      "Game:",
      "Game init done!"
    );
  }

  private _attachMouse() {
    this._stage.addChild(this._mouseLine!)
    this._stage.addChild(this._crosshair!)

    this._stage.interactive = true
    this._stage.on(
      "mousemove",
      (e: PIXI.InteractionEvent) => {
        this._updateMouseLocation(e.data.global)
      })

    this._stage.on(
      "mousedown",
      () =>
        this._fire()
    )

    this._stage.on(
      "mouseout",
      () => this._isRunning = false
    )

    this._stage.on(
      "mouseover",
      () => this._isRunning = this._player!.state !== ActorStates.died
    )
  }

  private _updateCrosshair() {
    if (
      this._mouseLocation &&
      this._isRunning &&
      this._player?.state !== ActorStates.died
    ) {
      this._crosshair!.x = this._mouseLocation!.x - this._crosshair!.width / 2
      this._crosshair!.y = this._mouseLocation!.y - this._crosshair!.height / 2

      // flip player based on crosshair position
      if (this._crosshair!.x < this._player!.toGlobal(this._stage).x) {
        this._player!.flip(false)
      } else {
        this._player!.flip(true)
      }

      // update player based on crosshair
      this._player!.updateCrosshairPosition(
        this._stage.toGlobal(this._crosshair?.position!)
      )

      // adapt crosshair scale based on player location
      this._crosshair!.adaptScale(this._player!.y, this._height)
    }
  }

  private _fire() {
    if (this._isRunning) {
      if (this._player?.fire()) {
        // hit location
        const impact = new Blood(this._resources, 2)
        const impactLocation = this._map!.toLocal(this._crosshair!)
        impact.x = impactLocation.x
        impact.y = impactLocation.y

        this._map?.backgroundContainer.addChild(impact);

        this._updateHud()
      }
    }
  }

  private _moveRight() {
    if (this._isRunning && this._player) {
      this._player.moveRight();
    }
  }

  private _moveLeft() {
    if (this._isRunning && this._player) {
      this._player.moveLeft();
    }
  }

  private _moveUp() {
    if (this._isRunning && this._player) {
      this._player.moveUp();
    }
  }

  private _moveDown() {
    if (this._isRunning && this._player) {
      this._player.moveDown();
    }
  }

  private _stopMoveVertically() {
    if (this._isRunning) {
      this._player?.stopMoveVertically();
    }
  }

  private _stopMoveHorizontally() {
    if (this._isRunning) {
      this._player?.stopMoveHorizontally();
    }
  }

  private _attachKeyboard() {
    const
      left = new Keyboard("ArrowLeft", true),
      up = new Keyboard("ArrowUp", true),
      right = new Keyboard("ArrowRight", true),
      down = new Keyboard("ArrowDown", true),

      k = new Keyboard("k", false),
      w = new Keyboard("w", false),
      r = new Keyboard("r", false),

      enter = new Keyboard("Enter", false);

    k.pressEvent = () => this._fire()
    w.pressEvent = () => this._switchWeapon()
    r.pressEvent = () => this._reload()

    right.pressEvent = () => this._moveRight()
    right.releaseEvent = () => {
      if (!left.isDown) {
        this._stopMoveHorizontally()
      }
    };

    left.pressEvent = () => this._moveLeft()
    left.releaseEvent = () => {
      if (!right.isDown) {
        this._stopMoveHorizontally()
      }
    };

    up.pressEvent = () => this._moveUp()
    up.releaseEvent = () => {
      if (!down.isDown) {
        this._stopMoveVertically()
      }
    };

    down.pressEvent = () => this._moveDown()
    down.releaseEvent = () => {
      if (!up.isDown) {
        this._stopMoveVertically()
      }
    };

    enter.pressEvent = () => {
      if (!this._isRunning) {
        this._init();
      }
    }
  }

  private _attachGamepad(): boolean {
    return !!Gamepads.init({
      // first gamepad
      0: {
        // button 1 pressed
        1: {
          pressEvent: () => {
            this._fire()
          }
        },
        // button 15 pressed
        15: {
          pressEvent: () => this._moveRight(),
          releaseEvent: () => this._stopMoveHorizontally()
        },
        // button 14 pressed
        14: {
          pressEvent: () => this._moveLeft(),
          releaseEvent: () => this._stopMoveHorizontally()
        },
        // button 12 pressed
        12: {
          pressEvent: () => this._moveUp(),
          releaseEvent: () => this._stopMoveVertically()
        },
        // button 13 pressed
        13: {
          pressEvent: () => this._moveDown(),
          releaseEvent: () => this._stopMoveVertically()
        },
        // button 9 pressed
        9: {
          pressEvent: () => {
            if (!this._isRunning) {
              this._init();
            }
          }
        }
      }
    })
  }

  /**
   * this will end the game
   * @private
   */
  private _end() {
    // game is not longer running dude
    this._isRunning = false;

    this._hud!.renderGameOver();
  }

  /**
   *
   * @private
   */
  private _detectCollisions() {
    for (const enemy of this._enemies) {
      if (ActorStates.died !== enemy.state &&
        ActorStates.died !== this._player!.state) {
        // does the player collide the enemy?
        if (this._player!.collides(enemy)) {
          const playerHit = this._player!.hitBy(enemy);

          if (playerHit.objectTookDamage) {
            this._drawBlood(this._player!);
            this._updateHud();
          }

          // we died and now lives left :(
          if (playerHit.died) {
            // end the game
            this._end();
          }
        }
      }
    }
  }

  /**
   *
   * @param actor: the bleeding actor
   * @private
   */
  private _drawBlood(
    actor: ActorBase
  ) {
    for (let i = 0; i < 5; i++) {
      const blood = new Blood(this._resources);

      blood.x = actor.x + MathHelper.getRandomInt(-20, 20);
      blood.y = actor.y + actor.height + MathHelper.getRandomInt(-20, 20);
      blood.rotation = MathHelper.getRandomInt(0, 10);

      this._map?.backgroundContainer.addChild(blood);
    }
  }

  /**
   *
   * @private
   */
  private _updateHud() {
    this._hud!.update();
  }

  /**
   *
   * @private
   */
  private _updateKI() {
    for (const enemy of this._enemies) {
      if (ActorStates.died !== enemy.state) {
        enemy.moveTowards(this._player!.position);
      }
    }
  }

  /**
   *
   * @private
   */
  private _moveActors() {

    const {_player} = this
    let playerMoved = false

    if (
      // do we have velocity?
      _player!.vy !== 0 &&
      _player!.y + _player!.vy > 0 &&
      // dont move lower than the bottom
      _player!.y + _player!.height + _player!.vy < this._height
    ) {
      _player!.y += _player!.vy;
      playerMoved = true
    }

    // move right
    if (_player!.vx > 0) {
      // dont walk further than the right border of the map
      if (
        _player!.realx +
        Math.abs(_player!.width) + 7 +
        _player!.vx < this._map!.width
      ) {
        _player!.x += _player!.vx;
        playerMoved = true
      }
    }
    // move left
    else if (_player!.vx < 0) {
      // detect left col
      if (
        _player!.realx + _player!.vx >= 0
      ) {
        _player!.x += _player!.vx;
        playerMoved = true
      }
    }

    if (playerMoved) {
      // yep, so try to scroll the map
      this._map?.shallScroll(
        _player!.realx,
        _player!.vx
      );
      this._updateCrosshair()

      this._player!.adaptScale(this._player!.y, this._height)
    }

    // move the enemies
    for (const enemy of this._enemies) {

      if (
        enemy.vx !== 0 ||
        enemy.vy !== 0 ||
        enemy.vr !== 0
      ) {
        enemy.x += enemy.vx;
        enemy.y += enemy.vy;
        enemy.rotation += enemy.vr;

        enemy.adaptScale(enemy.y, this._height)
      }
    }

  }

  private _outlineMouse() {
    if (
      this._mouseLine && this._mouseLocation &&
      this._player && this._map
    ) {
      this._mouseLine.clear()
      this._mouseLine.lineStyle(1, 0xffffff);

      // from
      this._mouseLine.position =
        this._player.weaponLocation

      // to
      const {x, y} = this._mouseLine.toLocal(this._mouseLocation)
      this._mouseLine.lineTo(
        x, y
      );

      this._mouseLine.endFill();
    }
  }

  private _switchWeapon() {
    if (this._isRunning) {
      this._player!.switchWeapon()
      this._updateHud()
    }
  }

  private _reload() {
    if (this._isRunning) {
      this._player!.reload()
      this._updateHud()
    }
  }

  private _updateMouseLocation(
    mouseLocation: PIXI.Point
  ) {
    if (this._isRunning) {
      this._mouseLocation = mouseLocation
      this._updateCrosshair()
    }
  }

  /**
   *
   * @param elapsed
   * @param delta
   * @public
   */
  public update(
    elapsed: number,
    delta: number
  ) {
    Gamepads.updateStatus();

    this._hud!.animate(delta);

    if (this._isRunning) {
      const elapsedSeconds = parseInt((elapsed / 1000).toString());

      // game has not started
      if (!this._startOffset) {
        this._startOffset = elapsedSeconds;
      }

      const correctedElapsedSeconds = (elapsedSeconds - this._startOffset);

      if (correctedElapsedSeconds !== this._elapsedSeconds) {
        this._elapsedSeconds = correctedElapsedSeconds;
        this._updateHud();
      }

      if (Game.timeout - correctedElapsedSeconds <= 0) {
        // times up!
        console.log("Time is up, dude!");
        this._end();
      }

      this._detectCollisions();
      this._updateKI();
      this._moveActors();

      const sort = (a: PIXI.DisplayObject, b: PIXI.DisplayObject) => {
        if (a.position.y < b.position.y) {
          return 1;
        }

        if (a.position.y > b.position.y) {
          return -1;
        }

        return 0;
      }

      // ensure z index for actors
      this._map?.actorContainer.children
        .sort(sort);

      if (Config.outlineActors) {
        // outline player
        AnimationHelper.outlineContainer(this._player!);

        // outline enemies
        for (const enemy of this._enemies) {
          AnimationHelper.outlineContainer(enemy);
        }

        // outline mouse line
        this._outlineMouse()
      }
    }
  }
}