import { AnimationBase } from "./animation-base"
import { AnimationHelper } from "../tools"

/**
 *
 */
export class Blood extends AnimationBase {

  constructor(
    resources: PIXI.IResourceDictionary,
    speed: number = 0.01
  ) {
    super(resources);
    const which = "things.blood"

    this._clips = {
      [which]: AnimationHelper.loadClip(
        this._resources,
        which,
        speed,
        1
      )
    };

    this._playClip(which, false, false);
  }
}