import PIXI from "pixi.js"
import { ActorStates } from "../tools";
import { AnimationBase } from "../objects"

/**
 * @class
 */
export class ActorBase extends AnimationBase {

  public outline?: PIXI.Graphics

  /**
   *
   * @type {null}
   * @protected
   */
  protected _skin?: string;

  /**
   *
   * @type {number}
   * @protected
   */
  protected _speed = 1;

  /**
   * the objects health
   * @type {number}
   * @protected
   */
  protected _health = 100;

  /**
   * the objects striking power
   * @type {number}
   * @protected
   */
  protected _power = 12;

  /**
   * @type {Symbol}
   * @protected
   */
  protected _state: Symbol = ActorStates.idle;

  /**
   * can we get git again helper
   * @type {boolean}
   * @protected
   */
  protected _canGetHitAgain = true;

  /**
   * @constructor
   */
  constructor(
    speed: number,
    resources: PIXI.IResourceDictionary,
    skin: string
  ) {
    super(resources);

    /**
     * set the actor skin
     * @type {String}
     * @protected
     */
    this._skin = skin;

    /**
     * the objects speed
     * @type {number}
     * @protected
     */
    this._speed = speed;
  }

  /**
   *
   * @returns {number}
   */
  get power() {
    return this._power;
  }

  /**
   *
   * @returns {Symbol}
   */
  get state() {
    return this._state;
  }

  /**
   *
   * @returns {number}
   */
  get health() {
    return this._health;
  }


  /**
   *
   * @param state
   * @protected
   */
  set state(state: Symbol) {
    console.log(this.constructor.name, "New state:", state);
    this._state = state;
  }

  /**
   *
   * @protected
   */
  protected _idle() {
    // dont go to idle if we are dead
    if (this._state !== ActorStates.died) {

      this.state = ActorStates.idle;

      this._playClip(
        this._skin + ".stance",
        true
      );
    }
  }

  /**
   *
   * @protected
   */
  protected _died() {
    this.state = ActorStates.died;

    // remove all velocity
    this._vy = 0;
    this._vx = 0;
    this._vr = 0;

    // play died clip
    this._playClip(
      this._skin + ".died",
      false,
      true
    )
  }

  /**
   * @public
   * @param actor
   * @returns {boolean}
   */
  public hitBy(
    actor: ActorBase
  ) {

    let died = false;
    let objectTookDamage = false;

    if (this._canGetHitAgain) {

      // lower the heal
      this._health -= actor.power;

      objectTookDamage = true;

      // after we got hit we cannot get hit again
      this._canGetHitAgain = false;

      // are we death?
      if (this._health < 0) {

        this._health = 0;

        // yep
        died = true;

        this._died();
      }
      // not died
      else {

        // force the player a little bit back on hit
        this._vx = actor._isFacingRight ? 5 : -5;

        // play the gothit clip
        this._playClip(
          this._skin + ".gothit",
          false
        )
          .then(() => {

            // animation ended!

            // remove the velocity
            this._vx = 0;

            // yep we can get hit again
            this._canGetHitAgain = true;

            // stand up and idle afterwards
            this._idle();
          });
      }
    }

    return {
      died,
      objectTookDamage
    };
  }

  public flip(
    direction?: boolean
  ) {
    this._setIsFacingRight(
      direction !== undefined ? direction : !this._isFacingRight
    )
  }
}