import * as PIXI from "pixi.js"
import { ObjectBase } from "../object-base"
import { Blood } from "../blood"

export class WeaponBase extends ObjectBase {
  protected _power: number
  protected _maxRounds: number
  protected _rounds: number
  protected _sprite?: PIXI.Sprite
  protected _roundsSprite?: PIXI.Sprite

  get rounds() {
    return this._rounds
  }

  get sprite() {
    return this._sprite
  }

  get roundsSprite() {
    return this._roundsSprite
  }

  constructor(
    resources: PIXI.IResourceDictionary,
    power: number,
    rounds: number
  ) {
    super(resources);

    this._power = power
    this._rounds = rounds
    this._maxRounds = rounds

    this.zIndex = 100
  }

  public fire(): boolean {

    if (this._rounds > 0) {
      // muzzle flash
      const muzzleFlash = new Blood(this._resources, 2)

      muzzleFlash.x = 80
      this.addChild(muzzleFlash);

      this._rounds--;

      return true
    }

    return false
  }

  public reload(): boolean {
    this._rounds = this._maxRounds

    console.log("reload")
    return true
  }
}