import * as PIXI from "pixi.js"
import {
  Beretta,
  Shotgun,
  WeaponBase
} from "../objects/weapons"
import { ActorBase } from "./actor-base"
import {
  ActorStates,
  AnimationHelper,
  MathHelper
} from "../tools"
import { Config } from "../config"


const weapons: {
  shotgun?: Shotgun,
  beretta?: Beretta
} = {}

/**
 *
 */
export class Player extends ActorBase {

  private _weapon: WeaponBase
  private _name: string = "Gordon"

  get weapon(): WeaponBase {
    return this._weapon
  }

  get name() {
    return this._name
  }

  /**
   *
   * @param resources
   * @param skin
   */
  constructor(
    resources: PIXI.IResourceDictionary,
    skin: string
  ) {
    super(
      3,
      resources,
      skin
    );

    this._baseScale = 0.7

    this._health = 77;

    // load the clips for this object
    this._clips[this._skin + ".stance"] = AnimationHelper.loadClip(this._resources, this._skin + ".stance", 0.1, 2);
    this._clips[this._skin + ".walk"] = AnimationHelper.loadClip(this._resources, this._skin + ".walk", 0.2, 2);
    this._clips[this._skin + ".punch"] = AnimationHelper.loadClip(this._resources, this._skin + ".punch", 0.3, 2);
    this._clips[this._skin + ".kick"] = AnimationHelper.loadClip(this._resources, this._skin + ".kick", 0.2, 2);
    this._clips[this._skin + ".gothit"] = AnimationHelper.loadClip(this._resources, this._skin + ".gothit", 0.2, 2);
    this._clips[this._skin + ".died"] = AnimationHelper.loadClip(this._resources, this._skin + ".died", 0.2, 2);

    weapons.shotgun = new Shotgun(resources)
    weapons.beretta = new Beretta(resources)

    // todo get this from equipment
    this._weapon = weapons.beretta
    this.addChild(this._weapon)

    // go to idle
    this._idle();
  }

  public reload() {
    // todo check equipment for shells
    this._weapon.reload()
  }

  public switchWeapon() {
    this.removeChild(this._weapon)

    if (this._weapon instanceof Shotgun) {
      this._weapon = weapons.beretta!
    } else {
      this._weapon = weapons.shotgun!
    }

    this.addChild(this._weapon)
  }

  public updateCrosshairPosition(
    crosshairPosition: PIXI.Point
  ) {
    const angle: number = this._isFacingRight ?
      MathHelper.angleBetweenRad(this.weaponLocation, crosshairPosition) :
      -1 * MathHelper.angleBetweenRad(crosshairPosition, this.weaponLocation)
    this._weapon.rotation = angle
  }

  get weaponLocation(): PIXI.Point {
    return this.toGlobal(
      this._weapon.position
    )
  }

  /**
   *
   * @returns {boolean}
   * @private
   */
  private _canBeControlled() {
    if (this._state === ActorStates.died) {
      console.log("Cant be controlled, player is dead");
      return false
    }

    return (
      this._state === ActorStates.idle ||
      this._state === ActorStates.walking
    );
  }

  private _logPlayerCoordinates() {
    if (Config.logPlayerCoordinates) {
      console.log(
        "x", this.x,
        "real x", this.realx,
        "y", this.y,
        "width", this.width,
        "height", this.height
      )
    }
  }

  /**
   *
   * @private
   */
  protected _died() {
    super._died();

    // todo: add dying animation
    // the died animation is way lower so we need to
    // adjust the hight a bit
    this.y += this.height / 2;
  }

  /**
   * @public
   */
  public fire(): boolean {
    if (this._canBeControlled()) {
      console.log("Png!");
      this.state = ActorStates.fire;

      this._vx = 0;
      this._vy = 0;

      const fire = this._weapon.fire()
      if (fire) {
        this._playClip(
          this._skin + ".punch",
          false
        )
          .then(() => {
            this._idle();
          });
        return true
      } else {
        this._idle()
      }
    }

    return false
  }

  /**
   * @public
   */
  public moveRight() {
    if (this._canBeControlled()) {
      this._vx = this._speed;
      this._playClip(
        this._skin + ".walk",
        true
      );
      this.state = ActorStates.walking;

      this._adaptScaleBasedOnOrientation();

      this._logPlayerCoordinates();
    }
  }

  /**
   * @public
   */
  public moveLeft() {
    if (this._canBeControlled()) {
      this._vx = -this._speed;
      this._playClip(
        this._skin + ".walk",
        true
      );
      this.state = ActorStates.walking;
      this._adaptScaleBasedOnOrientation();

      this._logPlayerCoordinates();
    }
  }

  /**
   * @public
   */
  public stopMoveHorizontally() {
    this._vx = 0;
    if (this._vy === 0 && this._canBeControlled()) {
      this._idle();
    }
  }

  /**
   * @public
   */
  public moveUp() {
    if (this._canBeControlled()) {
      this._vy = -this._speed;
      this._playClip(
        this._skin + ".walk",
        true
      );
      this.state = ActorStates.walking;

      this._logPlayerCoordinates();
    }
  }

  /**
   * @public
   */
  public moveDown() {
    if (this._canBeControlled()) {
      this._vy = this._speed;
      this._playClip(
        this._skin + ".walk",
        true
      );
      this.state = ActorStates.walking;

      this._logPlayerCoordinates();
    }
  }

  /**
   * @public
   */
  public stopMoveVertically() {
    this._vy = 0;
    if (this._vx === 0 && this._canBeControlled()) {
      this._idle();
    }
  }
}