import * as PIXI from "pixi.js"
import { MapBase } from "./map-base"

const {Sound} = require("pixi-sound").default

/**
 *
 */
export class HouseOutside extends MapBase {

  constructor(
    resources: PIXI.IResourceDictionary,
    width: number
  ) {
    super(resources, width, "maps.house")

    this._setup()
  }

  /**
   *
   * @private
   */
  protected _setup() {
    let city = new PIXI.Sprite(
      // @ts-ignore
      this._textures["house.png"]
    );

    city.scale.x = 1.3;
    city.scale.y = 1.3;

    this._backgroundContainer.addChild(city);

    const z1 = Sound.from('assets/sounds/zombie1.mp3');
    const z2 = Sound.from('assets/sounds/zombie1.mp3');

    setInterval(() => {
      z1.play();
    }, 15 * 1000)

    setInterval(() => {
      z2.play();
    }, 8 * 1000)
  }
}