import * as PIXI from "pixi.js";
import { ActorStates } from "../../tools/enums";
import { ActorBase } from "../actor-base"
import { AnimationHelper, MathHelper } from "../../tools"

/**
 *
 */
export class EnemyBase extends ActorBase {

  // time delta of last sprite flip
  private _lastFlip = 0;

  constructor(
    resources: PIXI.IResourceDictionary,
    skin: string,
    speed: number = 2
  ) {

    super(speed, resources, skin);

    this._isFacingRight = false;

    // load the clips for this object
    this._clips[this._skin + ".walk"] =
      AnimationHelper.loadClip(this._resources, this._skin + ".walk", 0.2, 2.2);
    this._clips[this._skin + ".gothit"] = this._clips[this._skin + ".walk"];
    this._clips[this._skin + ".died"] = this._clips[this._skin + ".walk"];

    // go to idle
    this._idle();

//		this._vr = 0.1;
//		this.pivot.set(Math.abs(this.width) / 2, this.height);

  }

  /**
   *
   * @private
   */
  protected _idle() {
    this.state = ActorStates.idle;
    this._playClip(
      this._skin + ".walk",
      true
    );
  }

  /**
   *
   * @private
   */
  protected _died() {
    super._died();

    // set proper death location
    this.y += this.height / 2;
    this.pivot.set(Math.abs(this.width) / 2, this.height);
    this.rotation = 90 * Math.PI / 180;

    //console.log("pivot", this.pivot);

    setTimeout(() => {
      this.parent.removeChild(this);
    }, 10 * 1000);
  }

  /**
   *
   * @returns {boolean}
   * @private
   */
  private _canFlipAgain() {
    let canFlip = true;
    if (Date.now() - this._lastFlip > 200) {
      canFlip = true;
      this._lastFlip = Date.now();
    }

    return canFlip;
  }

  /**
   *
   * @param target
   */
  public moveTowards(target: PIXI.Point) {

    /*
     this.pivot.set(this.width / 2, this.height);
     this.rotation += 0.1;

     return;*/

    const dist = MathHelper.distanceTo(this.position, target);

    let vx = 0;
    let vy = 0;

    if (dist > 1) {
      let angleToTarget = MathHelper.angleBetweenRad(this.position, target);

      vy = Math.sin(angleToTarget) * this._speed;
      vx = Math.cos(angleToTarget) * this._speed;

      // flip player over
      if (vx > 0 && !this._isFacingRight && this._canFlipAgain()) {

        //console.log("Enemy:", "flip left");
        this.flip();
      }
      // flip player over
      else if (vx < 0 && this._isFacingRight && this._canFlipAgain()) {

        //console.log("Enemy:", "flip right");
        this.flip();
      }
    }

    this._vy = vy;
    this._vx = vx;
  }
}