import * as PIXI from "pixi.js";

export class MathHelper {

  /**
   *
   * @param min
   * @param max
   * @returns {number}
   * @public
   */
  static getRandomInt(min: number, max: number) {
    return Math.floor(Math.random() * (max - min)) + min;
  }

  /**
   *
   * @param v2
   * @param v1
   * @returns {number}
   */
  static angleBetweenRad(
    v1: PIXI.Point,
    v2: PIXI.Point
  ) {
    return Math.atan2(v2.y - v1.y, v2.x - v1.x);
  }

  /**
   *
   * @param v1
   * @param v2
   * @returns {number}
   */
  static distanceTo(v1: PIXI.Point, v2: PIXI.Point) {
    return Math.sqrt(MathHelper.distanceToSq(v1, v2));
  }

  /**
   *
   * @param v2
   * @param v1
   * @returns {number}
   */
  static distanceToSq(v1: PIXI.Point, v2: PIXI.Point) {
    let dx = v1.x - v2.x,
      dy = v1.y - v2.y;

    return dx * dx + dy * dy;
  }
}