import * as PIXI from "pixi.js"
import { WeaponBase } from "./weapon-base"

/**
 *
 */
export class Shotgun extends WeaponBase {

  /**
   *
   * @param resources
   */
  constructor(
    resources: PIXI.IResourceDictionary
  ) {
    super(resources, 55, 6);

    const which = "weapons.shotgun"

    const res = resources![which]

    this._sprite = new PIXI.Sprite(
      res.textures!["shotgun.png"]!
    );

    this._roundsSprite = new PIXI.Sprite(
      res.textures!["shell.png"]!
    );

    this._sprite.scale.x = 0.2
    this._sprite.scale.y = 0.2

    this.x = -10
    this.y = 40

    // shotgun should be in the back so add it first
    this.addChild(this._sprite);
  }
}