import * as PIXI from "pixi.js"
import { MapBase } from "./map-base"

const {Sound} = require("pixi-sound").default

/**
 *
 */
export class MaensionOutside extends MapBase {

  constructor(
    resources: PIXI.IResourceDictionary,
    width: number
  ) {
    super(resources, width, "maps.maension")

    let starryNight = new PIXI.Sprite(
      this._textures!["tumblr_inline_n258q4r8c01qhwjx8.png"]
    );
    starryNight.scale.x = 2.56;
    starryNight.scale.y = 2.56;

    this._backgroundContainer.addChild(starryNight);

    let maension = new PIXI.Sprite(
      this._textures!["Maension_WIP.png"]
    );

    maension.y = -40
    maension.scale.x = 2;
    maension.scale.y = 2;

    this._backgroundContainer.addChild(maension);

    const tree = this._textures!["tree.png"]
    const tree1 = new PIXI.Sprite(tree);
    tree1.x = 100
    tree1.y = 350
    tree1.scale.x = 0.4
    tree1.scale.y = 0.4

    this._foregroundContainer.addChild(tree1)

    const tree_dead = this._textures!["tree_dead.png"]
    const tree2 = new PIXI.Sprite(tree_dead);
    tree2.x = 50
    tree2.y = 180
    tree2.scale.x = 0.4
    tree2.scale.y = 0.4

    this._backgroundContainer.addChild(tree2)

    this._setup()
  }

  /**
   *
   * @private
   */
  protected _setup() {
    const z1 = Sound.from('assets/sounds/zombie1.mp3');
    const z2 = Sound.from('assets/sounds/zombie1.mp3');

    setInterval(() => {
      z1.play();
    }, 15 * 1000)

    setInterval(() => {
      z2.play();
    }, 8 * 1000)
  }
}