import * as PIXI from "pixi.js"
import { Door } from "../objects"
import { MapBase } from "./map-base"

/**
 *
 */
export class Loading extends MapBase {

  private readonly _door: Door

  constructor(
    resources: PIXI.IResourceDictionary,
    width: number
  ) {
    super(resources, width);

    this._door = new Door(resources)
    this._door.position.x = (width / 2) - (this._door.width / 2)

    this._setup()
  }

  /**
   *
   * @private
   */
  protected _setup() {
    this._foregroundContainer.addChild(this._door);
  }
}