import * as PIXI from "pixi.js"
import { AnimationBase } from "./animation-base"
import { AnimationHelper } from "../tools"

/**
 *
 */
export class Door extends AnimationBase {

  /**
   *
   * @param resources
   */
  constructor(
    resources: PIXI.IResourceDictionary
  ) {
    super(resources);

    const which = "things.door"

    this._clips = {
      [which]: AnimationHelper.loadClip(
        this._resources,
        which,
        0.01,
        10
      )
    };


    this._clips[which].filters = [new PIXI.filters.NoiseFilter(0.2)]

    this._playClip(
      which,
      false,
      false
    );
  }
}