import * as PIXI from "pixi.js"

/**
 *
 */
export class MapBase extends PIXI.Container {

  private readonly _explorationOffset: number
  protected _textures?: PIXI.ITextureDictionary
  protected _backgroundContainer: PIXI.Container
  protected _actorContainer: PIXI.Container
  protected _foregroundContainer: PIXI.Container

  constructor(
    resources: PIXI.IResourceDictionary,
    protected _width: number,
    mapToLoad?: string
  ) {
    super();

    this._explorationOffset = this._width / 3;

    if (mapToLoad) {
      // load the clips for this object
      this._textures = resources[mapToLoad].textures;
    }

    /**
     * setup background
     * @type {PIXI.Container}
     * @private
     */
    this._backgroundContainer = new PIXI.Container();
    this.addChild(this._backgroundContainer);

    /**
     * setup actors
     * @type {PIXI.Container}
     * @private
     */
    this._actorContainer = new PIXI.Container();
    this.addChild(this._actorContainer);

    /**
     * setup foreground
     * @type {PIXI.Container}
     * @private
     */
    this._foregroundContainer = new PIXI.Container();
    this.addChild(this._foregroundContainer);
  }

  /**
   *
   * @returns {PIXI.Container}
   */
  get actorContainer() {
    return this._actorContainer;
  }

  /**
   *
   * @returns {PIXI.Container}
   */
  get backgroundContainer() {
    return this._backgroundContainer;
  }

  /**
   *
   * @private
   */
  protected _setup() {
    // stub
  }

  /**
   *
   * @param playerX
   * @param scrollAmount
   */
  public shallScroll(
    playerX: number,
    scrollAmount: number
  ) {
    if (
      // move right
      (scrollAmount > 0 && playerX + this._explorationOffset > this._width) ||
      // move left
      (scrollAmount < 0 && playerX - this._explorationOffset < this._explorationOffset)
    ) {
      // move map based on positive or negative scroll amount
      this.x -= scrollAmount

      // left bound
      if (this.x > 0) {
        this.x = 0
      }

      // right bound
      if (this.x < this._width - this.width) {
        this.x = this._width - this.width
      }
    }
  }
}