import * as PIXI from "pixi.js";
import { Config } from "../config"

/**
 *
 */
export class ObjectBase extends PIXI.Container {

  protected _baseScale: number = 1

  /**
   *
   * @type {Object}
   * @protected
   */
  protected _resources: PIXI.IResourceDictionary

  /**
   * Is the this one facing right?
   * @type {boolean}
   * @protected
   */
  protected _isFacingRight: boolean = true;

  /**
   * the object velocity x
   * @type {number}
   * @protected
   */
  protected _vx = 0;

  /**
   * the object velocity y
   * @type {number}
   * @protected
   */
  protected _vy = 0;

  /**
   * the objects rotation velocity
   * @type {number}
   * @private
   */
  protected _vr = 0;

  /**
   *
   * @param resources
   */
  constructor(
    resources: PIXI.IResourceDictionary
  ) {
    super();

    this._resources = resources;
  }

  get isFacingRight(): boolean {
    return this._isFacingRight
  }

  /**
   *
   * @returns {number}
   */
  get vx() {
    return this._vx;
  }

  /**
   *
   * @returns {number}
   */
  get vy() {
    return this._vy;
  }

  /**
   *
   * @returns {number}
   */
  get vr() {
    return this._vr;
  }

  /**
   *
   * @returns {Number}
   */
  get realx() {
    return this.x - (Math.abs(this.width) / 2);
  }

  /**
   *
   * @param actor
   * @public
   */
  public collides(
    actor: ObjectBase
  ) {
    const thisW = Math.abs(this.width)
    const actW = Math.abs(actor.width)
    const thisH = Math.abs(this.height)
    const actH = Math.abs(actor.height)

    const collides = !(
      actor.realx > (this.realx + thisW) ||
      this.realx > (actor.realx + actW) ||
      actor.y > (this.y + thisH) ||
      this.y > (actor.y + actH)
    )

    if (collides && Config.logCollisions) {
      console.log("this width/height", thisW, thisH);
      console.log("actor width/height", actW, actH);
    }

    return collides;
  }

  public adaptScale(
    y: number,
    height: number
  ) {
    const scaleOffset = height - y
    let scale = this._baseScale - scaleOffset / 1000

    this.scale.x = scale
    this.scale.y = scale

    this._adaptScaleBasedOnOrientation()
  }

  /**
   *
   * @private
   */
  protected _adaptScaleBasedOnOrientation() {
    let scale = this.scale.x;
    if (!this._isFacingRight && scale > 0) {
      this.scale.x = scale * -1;
    }

    if (this._isFacingRight && scale < 0) {
      this.scale.x = Math.abs(scale);
    }
  }
}