import * as PIXI from "pixi.js"
import { ActorBase } from "../actors"

/**
 * @class
 */
export class AnimationHelper {

  static loadClip(
    resources: PIXI.IResourceDictionary,
    which: string,
    speed: number,
    scale: number
  ): PIXI.AnimatedSprite {
    const frames: PIXI.Texture[] = [];
    for (const textureId in resources[which].textures) {
      let texture = resources[which]!.textures![textureId];
      frames.push(texture);
    }

    const clip = new PIXI.AnimatedSprite(frames);

    clip.animationSpeed = speed;
    clip.scale.x = scale;
    clip.scale.y = scale;
    clip.anchor.x = 0.5;

    return clip;
  }

  static outlineContainer(actor: ActorBase) {

    // do we have a filling
    if (actor.outline) {
      // yep remove if from child
      actor.removeChild(actor.outline);
      // delete it
      delete actor.outline;
    }

    // create new
    const outline = new PIXI.Graphics();
    outline.lineStyle(1, 0x000000);
    outline.drawRect(0, 0, actor.width, actor.height);
    outline.pivot.x = actor.width / 2;
    outline.endFill();

    actor.outline = outline;
    actor.addChild(outline);
  }
}